//////////////////////
// link
//////////////////////

// break-word

a, .alink, .btn-link {
  .select-none;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

// disabled

[disabled], .disabled {
  cursor: default;
  pointer-events: none;
}

//////////////////////
// btn
//////////////////////

.btn {
  .select-none;
  cursor: pointer;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  > span {
    display: inline-block;
  }
  &:active, &.active {
    z-index: @index-btn-active;
  }
}

// custom

.btn-close() {
  z-index: @index-btn-close;
}

//////////////////////
// drop
//////////////////////

.drop-container {
  display: inline-block;
  position: relative;
}

.drop-container-static {
  position: static;
  .drop,
  .drop-inner {
    width: 100%;
  }
}

.drop {
  .toggle-flex();
  position: absolute;
  z-index: @index-drop !important;
  // styles
  .drop-inner {
    position: relative;
  }
  .button {
    width: 100%;
    text-align: left;
    white-space: nowrap;
  }
  // others
  &.out {
    pointer-events: none;
  }
}

// before + after

.drop-before() {
  padding: @drop-space;
  left: auto;
  right: 100%;
}

.drop.drop-before {
  .drop-before() !important;
}

.drop-after() {
  padding: @drop-space;
  left: 100%;
  right: auto;
}

.drop.drop-after {
  .drop-after() !important;
}

// left + right

.drop-left() {
  padding: @drop-space 0;
  left: 0;
  right: auto;
}

.drop.drop-left {
  .drop-left() !important;
}

.drop-right() {
  padding: @drop-space 0;
  left: auto;
  right: 0;
}

.drop.drop-right {
  .drop-right() !important;
}

// bottom + top

.drop-bottom() {
  padding: @drop-space 0;
  top: 100%;
  bottom: auto;
}

.drop.drop-bottom {
  .drop-bottom() !important;
}

.drop-top() {
  padding: @drop-space 0;
  top: auto;
  bottom: 100%;
}

.drop.drop-top {
  .drop-top() !important;
}

// middle

.drop-middle() {
  padding-top: 0;
  padding-bottom: 0;
}

.drop.drop-middle {
  .drop-middle() !important;
}

.drop-middle-bottom() {
  top: auto;
  bottom: 0;
}

.drop.drop-middle-bottom {
  .drop-middle-bottom() !important;
}

.drop-middle-top() {
  top: 0;
  bottom: auto;
}

.drop.drop-middle-top {
  .drop-middle-top() !important;
}

//////////////////////
// overlay
//////////////////////

.overlay-outer {
  .toggle-flex();
  position: fixed;
  z-index: @index-overlay !important;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}

.overlay {
  // styles
  .display(flex);
  .flex-column();
  .flex-nowrap();
  .justify-content-center();
  position: relative;
  margin: auto; // @FIX http://stackoverflow.com/questions/33454533/cant-scroll-to-top-of-flex-item-that-is-overflowing-container
  width: 100%;
  min-height: 100vh;
  .overlay-inner {
    position: relative;
    z-index: @index-overlay !important;
    width: 100%;
    .align-self-center;
    > .btn-close + * {
      .margin(top, none) !important;
    }
  }
}

// default

.overlay-screen() {
  max-width: none !important;
  .overlay-inner {
    position: static;
    & + .xt-backdrop {
      display: none;
    }
  }
}

//////////////////////
// loader
//////////////////////

.loader-mask {
  position: absolute;
  z-index: @index-loader !important;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.loader-content {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
}

//////////////////////
// plugins
//////////////////////

// .xt-backdrop

.xt-backdrop {
  position: fixed;
  z-index: @index-backdrop !important;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
